.header__top {
    padding: 1.2rem 0 1.6rem;
}

.header__top__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    width: 14rem;
    height: 5rem;
    margin-right: 3rem;
}

.header__descriptor {
    max-width: 25rem;
    padding-left: 2rem;
    border-left: 1px solid rgba(72, 81, 85, 0.15);
    font-size: 1.6rem;
    margin-right: 2rem;
}

.header__search {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.header__search-btn {
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--white);
    color: var(--default);
}

@media (hover:hover) {
    .header__search-btn:hover {
        color: var(--blue);
    }
}

@media (hover:none) {
    .header__search-btn:active {
        color: var(--blue);
    }
}

.header__search-icon {
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__search-icon svg {
    width: 1.6rem;
    height: 1.6rem;
}

.header__search-form {
    display: none;
    transition: all .2s;
    opacity: 0;
    pointer-events: none;
    /* padding: 1rem 1.2rem; */
    transition: all .2s linear;
}

.header__search-form.active {
    display: flex;
}

.header__search-form .header__search-icon {
    margin-right: 1rem;
    margin-left: 1.2rem;
}

.header__search-form form {
    display: flex;
    align-items: center;
    gap: .5rem;
}

.header__search-input {
    font-size: 1.4rem;
    padding: 1rem 1.2rem 1rem 0;
    outline: none;
    color: var(--default);
}

.header__search {
    border-radius: 5rem;
    border: 1px solid var(--white);

    background-color: var(--white);
}

.header__search-submit {
    border-radius: 2rem;
    padding: .5rem 1.5rem;
}

.header__socials {
    margin-left: 3rem;
    display: flex;
    align-items: center;
}

.header__socials.hide {
    display: none;
}

.header__socials__text {
    font-family: 'Playfair Display', serif;
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--default);
    line-height: 1.2;
    margin-right: 2.8rem;

}

.header__socials__text span {
    color: var(--blue);
}

.header__socials__icons {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.header__socials__icon {
    width: 2.8rem;
    height: 2.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--default);
}

.header__socials__icon img {
    width: 1.4rem;
    height: 1.4rem;
    object-fit: contain;
}

@media (hover:hover) {
    .header__socials__icon:hover {
        background-color: var(--blue);
    }
}

@media (hover:none) {
    .header__socials__icon:active {
        background-color: var(--blue);
    }
}

.header__callback {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem 2rem;
    border-radius: 3.2rem;
    background-color: transparent;
    color: var(--default);
    font-size: 1.2rem;
    border: 1px solid rgba(72, 81, 85, 0.25);
    margin-left: 3.4rem;
}

.header__callback svg {
    width: 1.4rem;
    height: 1.4rem;
}

.header__callback span {
    font-weight: 500;

    font-family: 'FuturaPT', sans-serif;
}

@media (hover:hover) {
    .header__callback:hover {
        background-color: var(--default);
        color: var(--white);
        border-color: var(--default);
    }
}

@media (hover:none) {
    .header__callback:active {
        background-color: var(--default);
        color: var(--white);
        border-color: var(--default);
    }
}

.header__phone {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: 4.1rem;
}

.header__phone__link {
    font-family: 'FuturaPT Demi', sans-serif;
    font-size: 2rem;
    color: var(--default);
}

.header__phone__link span {
    color: var(--blue);
}

.header__phone__descriptor {
    font-size: 1.2rem;
    color: rgba(72, 81, 85, 0.5);
    font-family: 'FuturaPT Book', sans-serif;
}

.header__bottom {
    background-color: var(--grey);
}

.header__menu__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 4rem;
    position: relative;
}


.header__menu__link {
    font-size: 2rem;
    color: var(--default);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    gap: .6rem;

}

.header__menu__link svg {
    width: 1.4rem;
    height: 1.4rem;
}

@media (hover:hover) {
    .header__menu__link:hover {
        color: var(--blue);
    }
}

@media (hover:none) {
    .header__menu__link:active {
        color: var(--blue);
    }
}

.header__socials__icon_phone {
    display: none;
}
.burger__btn {
    display: none;
}
.header__menu__item {
    padding: 1.2rem 0;
}
