.services-dropdown {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    height: 49rem;
    background: transparent;
    display: flex;
    z-index: 100;
    box-shadow: 0 2px 24px 0 rgba(0,0,0,0.10);
    margin: 0;
    padding: 0;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s;
}
.header__menu__item--dropdown:hover .services-dropdown,
.header__menu__item--dropdown._open .services-dropdown {
    visibility: visible;
    opacity: 1;
}
.services-dropdown__categories {
    width: 31rem;
    background: var(--grey);
    height: 100%;
    padding: 1rem 2rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.services-dropdown__categories ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.services-dropdown__category {
    font-size: 1.8rem;
    color: var(--default);
    padding: 1rem;
    cursor: pointer;
    margin-bottom: .5rem;
    transition: all 0.15s, color 0.15s;
}
.services-dropdown__category--active,
.services-dropdown__category:hover {
    font-weight: 500;
    font-family: 'FuturaPT';
}
.services-dropdown__subcategories {
    flex: 1 1 auto;
    background: #fff;
    height: 100%;
    width: calc(100vw - 31rem);
    padding: 2rem;
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    position: relative;
}
.services-dropdown__subcategories-group {
    display: none;
    width: 100%;
    height: 100%;
}
.services-dropdown__subcategories-group.active {
    display: block;
}
.services-dropdown__columns {
    display: flex;
    gap: 4rem;
    height: 100%;
}
.services-dropdown__column {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    min-width: 18rem;
}
.services-dropdown__link {
    font-size: 1.4rem;
    color: var(--default);
    opacity: 0.75;
    text-decoration: none;
    transition: all 0.2s;
}
.services-dropdown__link:hover {
    color: var(--blue);
    opacity: 1;
}

.services-dropdown__column-group {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}
.services-dropdown__column-group-title {
    font-size: 1.4rem;
    color: var(--default);
    font-family: 'FuturaPT';
    font-weight: 500;
    text-decoration: none;
    transition: all 0.2s;
}
.services-dropdown__column-group-title:hover {
    color: var(--blue);
}
@media (max-width: 960px) {
    .services-dropdown__subcategories {
        padding: 2rem 1rem 2rem 2rem;
    }
    .services-dropdown__categories {
        padding: 2rem 0 2rem 2rem;
    }
}
@media (max-width: 500px) {
    .services-dropdown {
        flex-direction: column;
        height: auto;
        min-height: 40rem;
    }
    .services-dropdown__categories {
        width: 100vw;
        flex-direction: row;
        padding: 1rem 0 1rem 1rem;
        height: auto;
    }
    .services-dropdown__category {
        font-size: 1.5rem;
        padding: 1rem 1.5rem 1rem 0;
        margin-bottom: 0;
        margin-right: 1rem;
    }
    .services-dropdown__subcategories {
        width: 100vw;
        padding: 1rem 1rem 2rem 1rem;
    }
    .services-dropdown__columns {
        gap: 2rem;
    }
}